.item-catalog {
  background-color: $whitecolor; 
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__preview {
    padding: 0px 45px;
  }
  &__image {
    padding-top: 100%;
    position: relative;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      max-width: 100%;
      max-height: 100%;
      transition: all 1s ease;
      &:hover {
        transform: scale(1.08)
      }
    }  
  }
  &__body {
    flex: 1 1 auto;
    display: grid;
    grid-template-rows: 42px 50px 40px;
    text-align: center;
    padding-top: 10px;
    @media (max-height: $bpH) {
      grid-template-rows: 20px 47px 32px;
    }
    @media (max-width: $hd) {
      grid-template-rows: 35px 30px 25px;
      padding-top: 5px;
    }
  }
  &__title {
    padding: 0 2px;
    font-size: 17px;
    font-weight: 700;
    color: $maincolor;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    @media (max-height: $bpH) {
      font-size: 16px;
      -webkit-line-clamp: 1;
    }
    @media (max-width: $hd) {
      font-size: 14px;
    }
    &:hover {
      text-decoration: underline
    }
  }
  &__details {
    padding: 5px;
    align-self: center;
    color: $maincolor;
    @media (max-width: $hd) {
      display: none
    }
  }
  &__detail{
    margin-bottom: 3px;
    font-size: 14px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__price {
    font-size: 17px;
    line-height: 2;
    font-weight: 700;
    border-top: 1px solid $maincolor;
    color: $seagreencolor;
    @media (max-height: $bpH) {
      font-size: 16px;
      -webkit-line-clamp: 1;
    }
  }
  &__btn {
    align-self: center;
    justify-self: center;
    font-size: 14px;
    color: $maincolor;
    &:hover {
      color: $seagreencolor; 
      box-shadow: none;
    }
  }
}
