.product {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  z-index: 10;
  background: url(../assets/images/product-bg.png) center no-repeat, $whitecolor;
  color: $maincolor;
  padding: 30px 0;
  @media (max-width: $hd) {
    padding: 20px 0;
  }
  &__container {
    display: flex;
    flex-direction: column
  }
  &__top {
    min-height: 172px;
    display: grid;
    grid-template: 40px auto / 200px auto auto;
    gap: 0 50px;
    margin-bottom: 30px
  }
  &__preview {
    align-self: center;
    grid-row: 1/span 2;
    img {
      max-width: 100%;
      max-height: 100%
    }
  }
  &__title {
    font-size: 27px;
    font-weight: bold;
    @media (max-width: $hd) {
      font-size: 22px;
      top: -14px;
    }
  }
  &__price {
    font-size: 27px;
    font-weight: bold;
    justify-self: flex-end
  }
  &__details {
    align-self: center;
  }
  &__detail {
    font-size: 22px;
    span {
    font-weight: bold
    }
    @media (max-width: $hd) {
      font-size: 17px;
    }
  }
  &__bottom {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    p {
      max-width: 550px;
      margin-right: 20px
    }
  }
  &__code img {
    max-width: 150px;
  }
  p, .goback {
    color: $maincolor
  }
  .goback:hover {
    color: $seagreencolor
  }
  .blocks {
    height: auto
  }
  .blocks__item {
    border-color: $maincolor;
    p {
      font-size: 17px;
      padding: 0 10px;
      margin-bottom: 5px
    }
  }
  .blocks__title:before,
  .blocks__title:after {
    background-color: $maincolor;
  }
}