.rub:after {
  content: "₽";
  margin-left: 5px
}
html,body {
 scroll-behavior: smooth !important;
}
body {
  margin: 0;
  font-family: "Play";
  font-weight: 400;
  line-height: 1.2;
  color: $whitecolor;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.page {
  width: 100%;
  height: 100vh;
  padding: 200px 0 30px 0;
  background: url("../assets/images/hero.jpg") center no-repeat, $maincolor;
  position: relative;
  @media (max-height: $bpH) {
    padding: 170px 0 30px 0;
  }
  @media (max-width: $hd) {
    padding: 147px 0 20px 0;
  }
  &:after {
    content: "";
    display:block;
    width: 100%;
    height: 100%;
    background: rgba(11, 20, 35, 83%);
    position: absolute;
    top: 0;
    left: 0;
  }
}
.no-scroll {
  overflow: hidden;
}
.container {
  width: 100%;
  height: 100%;
  max-width: 1330px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
  z-index: 2
}
.btn {
  font-family: inherit;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 5px;
  transition: all .5s ease;
  cursor: pointer;
  &:hover {
    box-shadow: 0px 0px 10px $seagreencolor;
  }
}
p {
  font-size: 19px;
  color: $whitecolor;
  margin-bottom: 25px;
  @media (max-width: $hd) {
    font-size: 20px;
    margin-bottom: 15px
  }
}
.goback {
  font-size: 23px;
  position: absolute;
  bottom: 0;
  left: 15px;
  transition: all .3s ease;
  cursor: pointer;
  background: transparent;
  color: $whitecolor;
  &:hover {
    color: $seagreencolor
  }
  @media (max-width: $hd) {
    font-size: 20px;
  }
}
  