.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 160px;
  padding: 10px 0;
  @media (max-width: $hd) {
    height: 122px;
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    flex: 0 0 200px;
    margin-right: 15px;
    img {
      width: 200px;
      height: auto;
      @media (max-width: $hd) {
        width: 150px;
      }
    }
  }
  &__title {
    font-size: 42px;
    font-weight: bold;
    @media (max-width: $hd) {
      font-size: 36px
    }
  }
}
