.blocks {
  height: 100%;
  display: flex;
  flex-direction: column;
  &__items {
    padding: 20px 0;
  }
  &__item {
    margin-bottom: 35px;
    padding-bottom: 5px;
    border: 1px solid $whitecolor;
    border-top: 0;
    &:last-child {
      margin-bottom: 0
    }
    @media (max-width: $hd) {
      margin-bottom: 30px
    }
  }
  &__title {
    font-size: 25px;
    position: relative;
    top: -15px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    white-space: nowrap;
    &:before,&:after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background-color: $whitecolor
    }
    &:before {
      flex: 0 0 10px;
      margin-right: 15px
    }
    &:after {
      margin-left: 15px
    }
    @media (max-width: $hd) {
      font-size: 22px;
      top: -13.2px;
    }
  
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 10px
  }
  &__link {
    font-size: 18px;
    white-space:nowrap;
    padding: 4px 8px;
    border-radius: 5px;
    background-color: #0a1222;
    margin: 0 5px 5px 0;
    cursor: pointer;
    transition: all .2s linear;
    user-select: none;
    &:hover {
      box-shadow: 0px 0px 3px $seagreencolor
    }
    &.active {
      background-color: $seagreencolor;
      color: $whitecolor
    }
    &.disable {
      opacity: 0.6;
      pointer-events: none
    }
    @media (max-width: $hd) {
      font-size: 17px;
    }
  }
  &__footer {
    flex: 1 1 auto;
    display: flex; 
    align-items: center;
    justify-content: center
  }
  &__btn {
    background-color: $seagreencolor;
    color: $whitecolor;
    border-radius: 10px;
    font-size: 25px;
    font-weight: bold;
    padding: 5px 25px;
    box-shadow: 0px 5px 5px $maincolor;
    &:last-child {
      margin-left: 15px
    }
    @media (max-width: $hd) {
      font-size: 22px;
      padding: 5px 13px;
      border-radius: 5px
    }
  }
}