.home {
  height: 100%;
  &__container {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__btns {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center
  }
  &__btn {
    width: 100%;
    max-width: 539px;
    padding: 0 15px;
    height: 118px;
    font-size: 31px;
    line-height: 1.5;
    font-weight: bold;
    color: $maincolor;
    background-color: $whitecolor;
    margin-bottom: 60px;
    @media (max-width: $hd) {
      max-width: 500px;
      height: 100px;
      font-size: 25px;
      margin-bottom: 50px;
    }
    &::last-child {
      margin-bottom: 0
    }
  }
}
